.mobile-selectcity-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    h1 {
        font-size: 25px;
        cursor: pointer;
        span {
            color: #F89F46;
        }
        i {
            color: #F89F46;
            width: 25px;
            height: 25px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
        }
    }
    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 40px;
        background-color: #eee;
        border-radius: 10px;
        width: 300px;
        padding-bottom: 20px;
        span {
            color: #000;
            font-weight: bold;
            font-size: 20px;
            line-height: 1.5;
            width: 80%;
            text-align: center;
            border-radius: 6px;
            background-color: #fff;
            margin: 5px 0;
        }
    }
}